import React from 'react'
import SyntaxHighlighter from 'react-syntax-highlighter';
import { docco } from 'react-syntax-highlighter/dist/esm/styles/hljs';
import Button from '@mui/material/Button';
import Container from '@mui/material/Container';
import TextField from '@mui/material/TextField';
import Grid from '@mui/material/Grid';
import { Typography, Divider, Card } from '@mui/material';
import { JSONTree } from 'react-json-tree';
import { pink, lightBlue, teal, amber, grey } from '@mui/material/colors';
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';
import BookmarkAddIcon from '@mui/icons-material/BookmarkAdd';

var example_data = '%257B%2522glossary%2522%253A%257B%2522title%2522%253A%2522example%2520glossary%2522%252C%2522GlossDiv%2522%253A%257B%2522title%2522%253A%2522S%2522%252C%2522GlossList%2522%253A%257B%2522GlossEntry%2522%253A%257B%2522ID%2522%253A%2522SGML%2522%252C%2522SortAs%2522%253A%2522SGML%2522%252C%2522GlossTerm%2522%253A%2522Standard%2520Generalized%2520Markup%2520Language%2522%252C%2522Acronym%2522%253A%2522SGML%2522%252C%2522Abbrev%2522%253A%2522ISO%25208879%253A1986%2522%252C%2522GlossDef%2522%253A%257B%2522para%2522%253A%2522A%2520meta-markup%2520language%252C%2520used%2520to%2520create%2520markup%2520languages%2520such%2520as%2520DocBook.%2522%252C%2522GlossSeeAlso%2522%253A%255B%2522GML%2522%252C%2522XML%2522%255D%257D%252C%2522GlossSee%2522%253A%2522markup%2522%257D%257D%257D%257D%257D'

class Webstring extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
          input_value: '',
          output_value: '',
          data: {},
          steps: ["Strip", "JSON Decode"],
          expandTree: true
        };
    
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.loadExample = this.loadExample.bind(this);
        this.backspace = this.backspace.bind(this);

        this.funcmap = {
          "Unescape": [this._unescape, pink[500]] ,
          "Strip": [this._strstrip, lightBlue[500]],
          "URL Decode": [this._urldecode, teal[500]],
          "JSON Decode": [this._jsondecode, amber[500]]
        }
    }

    handleChange(event) {
        this.setState({input_value: event.target.value});
    }

    handleSubmit(event, func) {
      event.preventDefault();
      this.runSteps();
    }

    addStep(event, funcname) {
      event.preventDefault();
      this.setState(
        {steps: this.state.steps.concat([funcname])}, this.runSteps);
    }

    loadExample(event) {
      this.setState({input_value: example_data, steps: ["Strip", "Unescape", "URL Decode", "JSON Decode"]}, this.runSteps);
    }

    runSteps() {
      var output = this.state.input_value;
      for (const step in this.state.steps) {
        try {
          output = this.funcmap[this.state.steps[step]][0](output);
        } catch (err) {
          output = "Error in step: " + step + "\n" + err.message;
        }
      }
      this.setState({output_value: JSON.stringify(output), data: output});
      return output;
    }

    backspace(event) {
      this.setState({steps: this.state.steps.slice(0, -1)}, this.runSteps);
    }

    _urldecode(i) {
      return decodeURIComponent(i);
    }

    _jsondecode(i) {
      return JSON.parse(i);
    }

    _strstrip(i) {
      return i.trim();
    }

    _unescape(i) {
      return unescape(i);
    }

    automagic() {
      // infer the steps with a bfs
      var max_depth = 5;
      var steps = ['Strip', 'Unescape', 'URL Decode'];
      var queue = [[['Strip',], this.state.input_value], ];
      while (queue.length > 0) {
        // Try next step with JSON Decode
        var curr = queue.pop()
        try {
          var output = this._jsondecode(curr[1]);
          this.setState({steps: curr[0].concat('JSON Decode')});
          break;
        } catch (err) {
          // Cannot decode json, append next steps
          for (const s in steps) {
            if (curr[0].length < max_depth) {
              queue.push([curr[0].concat(steps[s]), this.funcmap[steps[s]][0](curr[1])]);
            }
          }
        }
      }
    }
    
    render() {
        return (
            <Container maxWidth="md" style={{fontFamily: 'monospace'}} spacing={3}>
              <Typography variant="h6" component="div" sx={{ flexGrow: 1, margin: 1, padding:1 }} >
                Json decode, URL decode, and automagic decode 
              </Typography>
              <Typography variant="caption" component="div" sx={{ flexGrow: 1, margin: 1, padding:1 }} >
                tl;dr If not sure, try <b>"Load Example"</b> first. <br />
                A lot of JSON from the Internet comes as garbled as it has been encoded zillions of times. Use this tool to decode it properly as a string or as JSON. Automagic will automatically guess the proper decoding steps.
              </Typography>
              <Card variant='outlined' sx={{m:1}}>
              <TextField
                id="standard-multiline-static"
                label="Input"
                multiline
                rows={16}
                defaultValue=""
                value={this.state.input_value}
                variant="standard"
                onChange={this.handleChange}
                sx={{ width: 1, fontFamily: 'monospace' }}
              />
              </Card>
              <Card variant='outlined' sx={{m:1}}>
              <Grid sx={{width: 1, display: 'flex', }} direction="column">
              <Divider/>
              <Typography variant="h6" component="div" sx={{ flexGrow: 1, margin: 1, padding:1 }} >
                Build Decoding Steps
              </Typography>
              <Grid direction='row'>
                {
                  this.state.steps.map((t,i) => 
                    <Button variant="outlined" text={t} sx={{ width: 0.2, m:1, color:this.funcmap[t][1] }}>{t}</Button>)
                    .reduce((prev, curr) => [prev, <ArrowForwardIosIcon></ArrowForwardIosIcon>, curr], null)
                }
                </Grid>
                <Divider/>
                <Grid>
                {Object.keys(this.funcmap).map((t,i) => 
                <Button variant="contained" text={t} onClick={event=>this.addStep(event, t)} sx={{ width: 0.2, m:1, bgcolor:this.funcmap[t][1] }}>{t}</Button>)}
                <Button variant="contained" text="Load Example" onClick={event => this.automagic()} sx={{ width: 0.2, m:1 }} >Auto Magic</Button>
                <Button variant="contained" text="Load Example" onClick={event => this.setState({steps: [], input_value: ''})} sx={{ width: 0.2, m:1 }} >Reset</Button>
                <Button variant="contained" text="Load Example" onClick={this.backspace} sx={{ width: 0.2, m:1 }} >Backspace</Button>
                </Grid>
              </Grid>
              </Card>
              <Card variant='outlined' sx={{m:1}}>
              <Typography variant="h6" component="div" sx={{ flexGrow: 1, margin: 1, padding:1 }} >
                Actions
              </Typography>
              <Button variant="contained" text="JSON Decode" onClick={event => this.handleSubmit(event, this._jsondecode)} sx={{ width: 0.2, m:1 }}>Run Steps</Button>
              <Button variant="contained" text="Load Example" onClick={this.loadExample} sx={{ width: 0.2, m:1 }} >Load Example</Button>
              </Card>
              <Card variant='outlined' sx={{m:1}}>
              <Typography variant="h6" component="div" sx={{ flexGrow: 1, margin: 1, padding:1  }} >
                Syntax
              </Typography>        
              <Card sx={{p:1}}>
              <SyntaxHighlighter language="json" style={docco} wrapLongLines={true}>{this.state.output_value}</SyntaxHighlighter>
              </Card>
              <Grid direction='row'>
              <Typography variant="h6" component="div" sx={{ flexGrow: 1 , margin: 1, padding:1 }} >
                Tree View
              </Typography> 
{/*               <Button onClick={this.setState({expandTree: true})}>Expand</Button>
 */}              </Grid>
              <Card sx={{p:1}}>
              <JSONTree data = {this.state.data} invertTheme={true} shouldExpandNode={(x,y,z)=>true}/>
              </Card>
              </Card>
            </Container>
          )
    }
}

export default Webstring;
