import * as React from 'react';
import PropTypes from 'prop-types';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Divider from '@mui/material/Divider';
import Drawer from '@mui/material/Drawer';
import IconButton from '@mui/material/IconButton';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemText from '@mui/material/ListItemText';
import MenuIcon from '@mui/icons-material/Menu';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import Webstring from './webstring';
import About from './about';

import { FacebookShareButton, TwitterShareButton } from "react-share";
import { FacebookIcon, TwitterIcon } from "react-share";

const drawerWidth = 200;
// TODO make those properties/parameters
const navItems = 
    ['String Tool', 'About'];

function SelectedDrawer(props) {
    const { children, value, index, ...other } = props;
    return (
        <div {...other}>
        {value === index && <Box component="main" sx={{ p: 3, width:1, display:'block' }}>
        <Toolbar />
        {children}
        </Box>}
        </div>
    )
}

function DrawerAppBar(props) {
  const { window } = props;
  const [mobileOpen, setMobileOpen] = React.useState(false);

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };  

  const drawer = (
    <Box onClick={handleDrawerToggle} sx={{ textAlign: 'center' }}>
      <Typography variant="h6" sx={{ my: 2 }}>
        Online Power Tools
      </Typography>
      <Divider />
      <List>
        {navItems.map((item, ind) => (
          <ListItem key={item} disablePadding>
            <ListItemButton sx={{ textAlign: 'center' }} value={ind} onClick={event => handleChange(event, ind)}>
              <ListItemText primary={item} />
            </ListItemButton>
          </ListItem>
        ))}
      </List>
    </Box>
  );

  const container = window !== undefined ? () => window().document.body : undefined;

  return (
    <Box sx={{ display: 'block' }}>
      <AppBar component="nav">
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            edge="start"
            onClick={handleDrawerToggle}
            sx={{ mr: 2, display: { sm: 'none' } }}
          >
            <MenuIcon />
          </IconButton>
          <Typography
            variant="h6"
            component="div"
            sx={{ flexGrow: 1, display: { xs: 'none', sm: 'block' } }}
          >
            Online Power Tools
          </Typography>
          <Box sx={{ display: { xs: 'none', sm: 'block' } }}>
            {navItems.map((item, ind) => (
              <Button key={item} sx={{ color: '#fff' }} value={ind} onClick={event => handleChange(event, ind)}>
                {item}
              </Button>
            ))}
            <Button sx={{ color: '#fff' }}>
            <FacebookShareButton
              url={"https://idewalt.github.io"}
              quote={"Share this power tool!"}
              hashtag={"#idewalt"}
              description={"Share this power tool!"}
            >
            <FacebookIcon size={20} round />
            </FacebookShareButton>
            </Button>
            <Button sx={{ color: '#fff' }}>
            <TwitterShareButton
              url={"https://idewalt.github.io"}
              quote={"Share this power tool!"}
              hashtag={"#idewalt"}
              description={"Share this power tool!"}
            >
            <TwitterIcon size={20} round/>
            </TwitterShareButton>
            </Button>
          </Box>
        </Toolbar>
      </AppBar>
      <Box component="nav">
        <Drawer
          container={container}
          variant="temporary"
          open={mobileOpen}
          onClose={handleDrawerToggle}
          ModalProps={{
            keepMounted: true, // Better open performance on mobile.
          }}
          sx={{
            display: { xs: 'block', sm: 'none' },
            '& .MuiDrawer-paper': { boxSizing: 'border-box', width: drawerWidth },
          }}
        >
          {drawer}
        </Drawer>
      </Box>
      
      <SelectedDrawer value={value} index={0}>
          <Webstring />
      </SelectedDrawer>
      <SelectedDrawer value={value} index={1}>
          <About />
      </SelectedDrawer>
    </Box>
  );
}

DrawerAppBar.propTypes = {
  /**
   * Injected by the documentation to work in an iframe.
   * You won't need it on your project.
   */
  window: PropTypes.func,
};

export default DrawerAppBar;
